
















import { Component, Vue } from 'vue-property-decorator';
import { TABS_DETAIL, Tabs } from '@/enum';
import { namespace } from 'vuex-class';
import { HomeState, HomeSetTab } from '@/store/interface';
import { Home } from '@/store/types';

const HomeModule = namespace('Home');
const GoodTopic = () => import('./GoodTopic.vue');
const AskTopic = () => import('./AskTopic.vue');
const DevTopic = () => import('./DevTopic.vue');
const JobTopic = () => import('./JobTopic.vue');
const ShareTopic = () => import('./ShareTopic.vue');
const AllTopic = () => import('./AllTopic.vue');

const Components = {
  [Tabs.ask]: AskTopic,
  [Tabs.share]: ShareTopic,
  [Tabs.good]: GoodTopic,
  [Tabs.job]: JobTopic,
  [Tabs.dev]: DevTopic,
  [Tabs.all]: AllTopic,
}
@Component
export default class ToolBar extends Vue {
  private tabs = TABS_DETAIL;
  @HomeModule.State((state: HomeState) => state.tab)
  private tab!: Tabs;
  @HomeModule.Mutation(Home.SET_TAB)
  private setTab!: HomeSetTab;
  get currentTabComponent() {
    return Components[this.tab];
  }
}
